@import "../../../colors.scss";

.news-post{
  &-container{
    border: 2px solid $grey-2;
    color: $green;
    width: 700px;
    max-width: 700px;

  }
  &-content{
    color: $white;
    a{
      color: $green;
      text-decoration: none;
      &:hover{
        color: $pink
      }

    }
  }
  &-title{
      color: $green;
      text-decoration: none;
      font-size: 28px;
      min-height: 70px;
      min-width: 27px;
    &:hover{
      color: $pink
    }
  }
}
